<template>
  <div class="progress-bar-container hide-sm">
    <div class="p">{{completeInt}}% complete</div>
    <div class="bar-outer">
      <div class="bar-progress" v-bind:style="barSize"></div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['complete'],
    data () {
      return {
        completeInt: 0
      }
      
    },
    computed: {
      barSize () {
        return {
          'width': this.completeInt + '%' 
        }
      }
    },
    watch: {
      complete () { // increment progress bar
        let curr = (this.complete * 100).toFixed()
        if (curr > 96) curr = 100
        let inc = setInterval(() => {
          if(this.completeInt >= curr) {
            clearInterval(inc)
          } else {
            this.completeInt = this.completeInt + 1
          }
        }, 150)
      }
    }
  }
</script>

<style scoped lang="scss">
  @import '../../styles/variables';

  .progress-bar-container {
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    margin-top: 12px;
    margin-right: 80px;
    width: 220px;
    @media screen and (max-width: $md-max) {
      position: absolute;
    }
  }

  .bar-outer {
    position: relative;
    width: 100%;
    height: 6px;
    background: $light-grey;
  }

  .bar-progress {
    position: absolute;
    width: 25%;
    height: 100%;
    background: #51575D;
    transition: all 0.8s ease;
  }
</style>


