<template>
  <div v-bind:class="isSurvey">

    <div class="gutter-horizontal">

      <!-- TODO: refactor to only hold a few pages at a time (ideally, current, next, prev.) -->
      <!-- currently crashes on mobile -->
      <!-- can handle 25, crashes at 37 -->

        <div v-for="(page, index) in pages" v-bind:key="index" class="slide" v-bind:class="[slide_active(index).class, slide_in_queue(index)]">
          <component v-bind:is="page.template" v-bind:content="page.content" v-bind:title="page.title" v-bind:active="slide_active(index).bool">
            {{page.content}}
          </component>
        </div>

    </div>
    
    <progress-bar v-bind:complete="percentComplete"></progress-bar>

    <navbar></navbar>
    
    <notification v-bind:state="notification_state" v-bind:captionState="caption_state"></notification>

  </div>
</template>

<style scoped lang="scss">
  @import '../styles/variables';

  .slide {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    transition: transform 0.9s ease-in-out, opacity 1.8s ease-in-out;
    &.page-inactive {
      transform: translateX(-200%);
      opacity: 0;
      @media screen and (max-width: $md-max) {
        position: fixed;
        width: calc(100% - 80px - 80px);
        margin-left: 80px;
      }
      @media screen and (max-width: $sm-max) {
        width: 100%;
      }
    }
    @media screen and (max-width: $md-max) {
      position: relative;
      height: auto;
      width: auto;
    }
  }

  .pdf-link {
    position: absolute;
    bottom: 0;
    left: 0;
    // width: 100%;
    // height: 24px;
    background: $debug;
    text-align: center;
    z-index: 99 !important;
  }
</style>

<script>
  import navbar from '../components/organisms/navbar'
  import progressBar from '../components/organisms/progressBar'
  import notification from '../components/organisms/notification'
  import pages from './pages'

  import contentful_init from '../contentful'
  
  export default {
    data () {
      return {
        isSurvey: ''
      }
    },
    computed: {
      // TESTING FOR:
      // what is resilience?
      // individual factors that promote resilience
      // check and divide by half
      // first_half () {
      //   let len = this.$store.state.pages.length
      //   return this.$store.state.page < (len / 2) -1
      // },
      pages () {
        return this.$store.state.pages
      },
      notification_state () {
        return this.$store.state.notification_state
      },
      caption_state  () {
        return this.$store.state.checkbox
      },
      percentComplete () {
        let curr = this.$store.state.page
        let len = this.$store.state.pages.length
        return curr/len
      }
    },
    methods: {
      slide_active (page) {
        let current_page = this.$store.state.page
        if (current_page !== page) {
          return {
            class: 'page-inactive',
            bool: false
          }
        } else {
          return {
            class: '',
            bool: true
          }
        }
      },
      slide_in_queue (this_page) {
        // set to display: none if not directly before or after slide
        let current_page = this.$store.state.page

        if (this_page < current_page - 2 || this_page > current_page + 2) {
          return 'hide-all'
        } else {
          return ''
        }
        
      }
    },
    components: {
      navbar,
      notification,
      'progress-bar': progressBar,
      ...pages
    },
    mounted () {
      contentful_init(this, 'set_pages')

      setTimeout(() => {
        console.log(this.$store.state.pages.length)
      }, 2500)
    }
  }
</script>



